import React from 'react'
import { Box } from '@material-ui/core'

const FullPageSpinner = ({ size }) => (
  <Box width="100%" padding="250px 0" textAlign="center">
    <Spinner size={size} />
  </Box>
)

const Spinner = ({ size }) => (
  <svg width={size} height={size} viewBox="0 0 38 38" xmlns="http://www.w3.org/2000/svg" stroke="grey">
      <g fill="none" fillRule="evenodd">
          <g transform="translate(1 1)" strokeWidth="2">
              <circle strokeOpacity=".5" cx="18" cy="18" r="18"/>
              <path d="M36 18c0-9.94-8.06-18-18-18">
                  <animateTransform
                      attributeName="transform"
                      type="rotate"
                      from="0 18 18"
                      to="360 18 18"
                      dur="1s"
                      repeatCount="indefinite"/>
              </path>
          </g>
      </g>
  </svg>
)

export const ButtonSpinner = ({ size }) => (
  <Spinner size={size} style={{position: 'absolute', top: '5px', right: '30px'}} />
)

export default FullPageSpinner